body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right,#ffa17f,#00223e);
  background: #abbaab;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #e9e4e4 90%, #525253 10%);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #e9e4e 90%, #525253 10%); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
